.Table {
  font-size: 1rem;
}

.Table .camper  {
  display: -ms-flexbox;
  display: flex;
  -ms-flex-wrap: wrap;
      flex-wrap: wrap;
  min-width: 350px;
}

.Table__header {
  text-align: center;
  font-weight: 500;
}

.Table__header__item {
  background-color: #006400 !important;
  color: #FFF;
}

.Table .camper__rank,
.Table .camper__username,
.Table .camper__recent-points,
.Table .camper__alltime-points {
  -webkit-box-sizing: border-box;
          box-sizing: border-box;
  background-color: #e2e6e9;
  -ms-flex: 1 1 auto;
      flex: 1 1 auto;
  border: 3px solid #FFF;
  padding: .5em 0;
  overflow: hidden;
}

.Table .camper__rank {
  width: 2em;
}

.Table .camper__username,
.Table .camper__recent-points,
.Table .camper__alltime-points {
  width: 30%;
  padding-left: 1em;
  padding-right: 1em;
}

.Table .camper__rank,
.Table .camper__recent-points,
.Table .camper__alltime-points {
  text-align: center;
}



